ol, ul {
  padding-left: 30px;
}

.board-container {
  position: relative;
  overflow: hidden;
}

.board-row {
  display: flex;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.board-line-container {
  position: relative;
  z-index: 2;
}

.board-foundation-container {
  position: absolute;
  z-index: 0;
  left: -17px;
  top: -17px;
}

.board-border {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 17px solid #282c34;
  top: 0;
  left: 0;
}

.status {
  margin-bottom: 10px;
  font: 14px "Century Gothic", Futura, sans-serif;
}

.square {
  background: #fff;
  border: 1px solid #999;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.board-line {
  background: transparent;
  border: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}
/* .mouse-navigation .square:focus {
  background: #ddd;
} */

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.active-history {
  font-weight: bold;
}

.kbd-navigation .winner-piece:focus, .mouse-navigation .winner-piece:focus, .winner-piece {
  background: red;
}

.piece {
  width: 70%;
  height: 70%;
  border-radius: 50%;
  margin-top: 15%;
  display: inline-block;
  transition: .2s;
}

.piece-black {
  background: #1a1d24;
  border: 1px solid #262930;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.piece-white {
  background: #fff;
  border: 1px solid #ddd;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.piece-winner {
  background: #47b173;
  border: 1px solid #157c40;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}
